import { useAuthUser } from 'react-auth-kit'

import { Box, Button, Modal, TextareaAutosize, Typography, styled } from '@mui/material'
import { useTheme } from '@mui/material/styles'
import useMediaQuery from '@mui/material/useMediaQuery'

import type { QuestionAndAnswer } from 'src/models/Form'

interface AnswerModalProps {
  open: boolean
  onClose: React.MouseEventHandler<HTMLButtonElement>
  questionAndAnswers: QuestionAndAnswer[]
}

const AnswerModal: React.FC<AnswerModalProps> = ({ open, onClose, questionAndAnswers }) => {
  const theme = useTheme()
  const isNotMobile = useMediaQuery(theme.breakpoints.up('sm'))
  const authInfo = useAuthUser()()

  return (
    <Modal open={open} onClose={onClose}>
      <Box sx={style}>
        <Box justifyContent="center">
          <Typography variant="h3" mb={1} textAlign={'center'}>
            {authInfo?.name} 님 의 지원서
          </Typography>
          <Typography variant="subtitle2" textAlign="center">
            {authInfo?.studentId} / {authInfo?.department}
          </Typography>
          <Typography variant="subtitle2" mb={2} textAlign={'center'}>
            {authInfo?.email} / {authInfo?.phoneNumber}
          </Typography>
        </Box>
        <Box display="flex" alignItems="center" justifyContent="space-around">
          <Box width={isNotMobile ? '60%' : '90%'}>
            {questionAndAnswers !== null ? (
              questionAndAnswers.map((questionandanswer, index) => {
                return (
                  <Box key={index}>
                    <Typography variant="subtitle1">{questionandanswer.question}</Typography>
                    <CustomTextareaAutosize maxRows={4} minRows={3} value={questionandanswer.answer} disabled />
                  </Box>
                )
              })
            ) : (
              <Typography textAlign={'center'}>지원서 문항이 존재하지 않습니다.</Typography>
            )}
          </Box>
        </Box>
        <Box marginTop={2} display="flex" justifyContent="center">
          <Button variant="outlined" onClick={onClose}>
            닫기
          </Button>
        </Box>
      </Box>
    </Modal>
  )
}

export default AnswerModal

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '80%',
  maxWidth: '600px',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4
}

const CustomTextareaAutosize = styled(TextareaAutosize)`
  margin: 10px 0px;
  padding: 8px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-family: 'Pretendard';
`
